:root {
  --color-white: #ffffff;
  --color-black: #000000;
  --text-black: #22252F;
  --border-gray: #2D333C;
  --gray-400: #E2E3EA;
  --text-gray-light: #7F808A;
  --text-gray: #8E8F9E;
  --bg-soft: #efefef;
  --bg-blue: #e0f2f7;
  --bg-green: #e6ffe6;
  --bg-red: #ffe6e6;
  --bg-hover: #f9f9f9;
  --color-green: #008000;
  --color-red: #eb3423;
}

.barrier-configurator .barrier-popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10000;
}

.barrier-configurator .barrier-popup--active {
  display: flex;
  align-items: center;
  justify-content: center;
}

.barrier-configurator .barrier-popup__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
}

.barrier-configurator .barrier-popup__content {
  font-family: Manrope-Regular, "Helvetica Neue", Arial, -apple-system, system-ui, sans-serif, system-ui;
  position: relative;
  background-color: #F3F4F8;
  padding: 65px 16px 100px;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1;
}

.barrier-configurator .barrier-popup__close {
  position: absolute;
  top: 0px;
  right: 0px;
  background: none;
  border: none;
  cursor: pointer;
  transition: all 0.3s;
  font-size: 32px;
  line-height: 1;
  color: var(--text-gray);
  padding: 0;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  mask-image: url("../../img/close.svg");
  background: #7F808A;
  mask-size: 36px;
  mask-position: center;
  mask-repeat: no-repeat;
}

.barrier-configurator .barrier-popup__close:hover {
  background: var(--text-black);
}

.barrier-configurator .barrier-popup__illustration {
  text-align: center;
  height: 134px;
}

.barrier-configurator .barrier-popup__illustration img {
  height: auto;
  object-fit: cover;
  width: 174px;
}

.barrier-configurator .barrier-popup__title {
  font-size: 24px;
  line-height: 32px;
  font-weight: 700;
  text-align: center;
  margin-bottom: 8px;
  color: var(--text-black);
}

.barrier-configurator .barrier-popup__description {
  font-size: 14px;
  line-height: 20px;
  font-weight: 600;
  text-align: center;
  color: var(--text-gray);
  margin-top: 0;
  margin-bottom: 20px;
}

.barrier-configurator .barrier-popup__form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.barrier-configurator .barrier-popup__field {
  display: flex;
  flex-direction: column;
}

.barrier-configurator .barrier-popup__label {
  font-size: 12px;
  line-height: 14px;
  color: var(--text-black);
  margin-bottom: 4px;
  font-weight: 600;
}

.barrier-configurator .barrier-popup__required {
  color: var(--text-black);
}

.barrier-configurator .barrier-popup__input, .barrier-configurator .barrier-popup__textarea {
  padding: 8px 8px;
  border: 1px solid transparent;
  border-radius: 8px;
  font-size: 14px;
  line-height: 20px;
  font-family: inherit;
  background-color: #fff;
  transition: all 0.3s;
}

.barrier-configurator .barrier-popup__input::placeholder, .barrier-configurator .barrier-popup__textarea::placeholder {
  color: var(--text-gray-light);
  font-weight: 400;
}

.barrier-configurator .barrier-popup__input:focus, .barrier-configurator .barrier-popup__textarea:focus {
  outline: none;
  border-color: #333;
}

.barrier-configurator .barrier-popup__input {
  height: 44px;
}

.barrier-configurator .barrier-popup__input--error {
  border-color: var(--color-red);
}

.barrier-configurator .barrier-popup__error {
  font-size: 12px;
  line-height: 14px;
  color: var(--color-red);
  margin-top: 4px;
}

.barrier-configurator .barrier-popup__textarea {
  height: 120px;
  resize: vertical;
  min-height: 100px;
}

.barrier-configurator .barrier-popup__submit {
  margin-top: 12px;
  position: fixed;
  bottom: 32px;
  left: 16px;
}

@media screen and (min-width: 451px) {
  .barrier-configurator .barrier-popup__form {
    gap: 28px;
  }
  .barrier-configurator .barrier-popup__content {
    max-width: 564px;
    padding: 52px 32px;
    height: auto;
    border-radius: 40px;
  }
  .barrier-configurator .barrier-popup__close {
    top: 12px;
    right: 12px;
    mask-size: 48px;
  }
  .barrier-configurator .barrier-popup__illustration {
    margin-bottom: 28px;
  }
  .barrier-configurator .barrier-popup__illustration img {
    width: 230px;
  }
  .barrier-configurator .barrier-popup__title {
    margin-bottom: 8px;
    font-size: 32px;
    line-height: 40px;
  }
  .barrier-configurator .barrier-popup__description {
    font-size: 18px;
    line-height: 24px;
    margin-bottom: 28px;
  }
  .barrier-configurator .barrier-popup__label {
    font-size: 16px;
    line-height: 20px;
  }
  .barrier-configurator .barrier-popup__textarea {
    padding-top: 18px;
    height: 190px;
  }
  .barrier-configurator .barrier-popup__input, .barrier-configurator .barrier-popup__textarea {
    padding: 12px 20px;
    font-size: 18px;
    line-height: 24px;
  }
  .barrier-configurator .barrier-popup__input {
    height: 52px;
  }
  .barrier-configurator .barrier-popup__submit {
    position: static;
    width: auto;
  }
}

@media screen and (min-width: 601px) {
  .barrier-configurator .barrier-popup__content {
    max-width: 660px;
    padding: 52px 80px;
    border-radius: 40px;
  }
  .barrier-configurator .barrier-popup__close {
    top: 24px;
    right: 24px;
    mask-size: 60px;
  }
}

@media screen and (min-width: 713px) {
  .barrier-configurator .barrier-popup__form {
    gap: 20px;
  }
  .barrier-configurator .barrier-popup__content {
    max-width: 730px;
    padding-left: 120px;
    padding-right: 120px;
    border-radius: 40px;
  }
  .barrier-configurator .barrier-popup__close {
    top: 24px;
    right: 24px;
    mask-size: 60px;
    background-color: var(--border-gray);
  }
  .barrier-configurator .barrier-popup__illustration img {
    width: 230px;
  }
  .barrier-configurator .barrier-popup__title {
    font-size: 32px;
    line-height: 40px;
  }
}

@media screen and (max-width: 712px) {
  .barrier-configurator .barrier-popup__submit {
    font-size: 18px;
    line-height: 28px;
    position: sticky;
    border-radius: 16px;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1000;
  }
}
